<template>
  <div>
    <div class="fixed" v-for="(item, index) in shortList" :data-index="index">
    <div v-show="index === sActive">{{ item }}</div>
    </div>
  </div>
</template>
<script type="text/javascript">
  export default{
    data() {
      return {
        sActive: 0
      }
    },
    props: {
      data: {
        type: Array,
        dafault: []
      }
    },
    computed: {
      shortList() {
        return this.data.map((item) => {
          return item.title
        })
      }
    },
    methods: {
      _countSite(i) {
        this.sActive = i
      }
    }
  }
</script>
<style scoped>
  .fixed{
   width: 100%;
   height: 100%;
 }
</style>


