<template>
  <ul class="siderBarUl" @touchstart="_touchStart($event)">
    <li class="siderBarLi" v-for="(item, index) in shortList" :data-index="index" :class="{sActive:sActive === index}" @touchmove="_touchMove($event)">{{ item }}</li>
  </ul>
</template>
<script type="text/javascript">
  export default{
    data() {
      return {
        sActive: 0
      }
    },
    props: {
      data: {
        type: Array,
        dafault: []
      }
    },
    computed: {
      shortList() {
        return this.data.map((item) => {
          return item.title.substr(0, 1)
        })
      }
    },
    methods: {
      _touchStart(e) {
        e.preventDefault()
        let scroll = parseInt(e.target.getAttribute('data-index'))
        this.$emit('scroll', scroll)
        this.sActive = scroll
        // scroll = null
      },
      _touchMove(e) {
        e.preventDefault()
        requestAnimationFrame(() => {
          let scroll = parseInt(e.target.getAttribute('data-index'))
          this.$emit('scroll', scroll)
          this.sActive = scroll
          // scroll = null
        })
      },
      _countSite(i) {
        this.sActive = i
      }
    }
  }
</script>
<style scoped>
  .siderBarUl{
    width: 100%;
    height: 100%;
  }
  .siderBarLi{
    width: 100%;
    height: 18px;
    font-size: 12px;
    line-height: 18px;
    text-align: center;
    color: #999;
  }
  .siderBarUl .sActive{
    color: #ffcd32;
  }
</style>


