<template>
 <div>
  <div class="singerBody" ref="singerBody">
    <v-scroll ref="scroll" v-on:updateY="_updateSideBar">
      <div v-for="group in singers" class="singerList" ref="singerList">
        <h3>{{ group.title }}</h3>
        <ul class="singerUl">
          <li v-for="item in group.items" @click="_touchStart(item)">
          <div class="loading img-warp">
            <img :data-src="item.avatar" class="lazy-img" :onerror="loading">
            </div>
           <!--  :src="item.avatar" -->
            <span>{{ item.name }}</span>
          </li>
        </ul>
      </div>
      <div id="footer" v-show="singers.length > 0"><span class="author">我是有底线的</span> <span class="email">email：530366891@qq.com</span></div>
      <div id="footer" v-if="singers.length <= 0">正在努力获取数据</div>
    </v-scroll>
  </div>
  <div class="sideBar" v-show="singers.length > 0">
    <s-list :data="singers" v-on:scroll="_calculateHeight" ref="sList"></s-list>
  </div>
  <div class="titleBar" :class="{opacity: showTitle !== 0}">
<!--   :class="{opacity: showTitle !== 0}" -->
    <s-title :data="singers" ref="sTitle"></s-title>
  </div>
  <router-view></router-view>
</div>
</template>
<script>
  import VScroll from 'base/scroll/scroll'
  import Singer from 'common/js/singer'
  import SList from 'base/sideList/sideList'
  import STitle from 'base/singerTitle/singerTitle'
  import {getSingerList} from 'api/singer'
  import {ERR_OK} from 'api/config'
  import {mapMutations} from 'vuex'

  const HOT_NAME = '热门'
  const HOT_NUMBER = 10
  const IMAGE_HEIGHT = 100

  export default{
    data() {
      return {
        singers: [],
        showTitle: 0,
        loading: '',
        lazyLoop: 0
      }
    },
    created() {
      this._getSingerList()
    },
    mounted() {
      const that = this
      that.cunt = 0
      let timer = setInterval(() => {
        that.cunt++
        if (that.cunt > 10) {
          that.cunt = null
          clearInterval(timer)
        }
        that._init()
        if (that.domscroll && that.domSingerList) {
          that.cunt = null
          that._domHeghtList()
          clearInterval(timer)
        }
      }, 20)
    },
    methods: {
      _init() {
        this.domSingerList = this.$refs.singerList
        this.domscroll = this.$refs.scroll
        this.domscrollH = this.domscroll.wrapH + IMAGE_HEIGHT
        // this.scrollH = this.domscroll.offsetHeight
        // console.log(this.domscroll.wrapH)
      },
      _lazyLoadInit() {
        this.images = document.getElementsByClassName('lazy-img')
        this.lazyNumber = 0
        this.loading = 'this.src="' + require('../../assets/logo/loading.png') + '"'
        this._lazyLoad()
      },
      _lazyLoad() {
        if (this.lazyNumber >= this.images.length) {
          return
        }
        // let images = document.getElementsByClassName('lazy-img')
        if (this.images[this.lazyNumber].getBoundingClientRect().top < this.domscrollH) {
          this.lazyLoop = 1
          this.images[this.lazyNumber].src = this.images[this.lazyNumber].getAttribute('data-src')
          this.images[this.lazyNumber].style.visibility = 'visible'
          this.lazyNumber++
          this._lazyLoad()
        } else {
          this.lazyLoop = 0
          return
        }
      },
      _domHeghtList() {
        this.domHeightArray = []
        let H = 0
        for (let i = 0; i < this.domSingerList.length; i++) {
          H += this.domSingerList[i].offsetHeight
          this.domHeightArray.push(H)
        }
        H = null
      },
      _getSingerList() {
        getSingerList().then((res) => {
          if (res.code === ERR_OK) {
            this.singers = this._normSingerList(res.data.list)
            requestAnimationFrame(() => {
              this._lazyLoadInit()
            })
          }
        })
      },
      _normSingerList(list) {
        let map = {
          hot: {
            title: HOT_NAME,
            items: []
          }
        }
        list.forEach((item, index) => {
          if (index < HOT_NUMBER) {
            map.hot.items.push(new Singer({
              name: item.Fsinger_name,
              id: item.Fsinger_mid
            }))
          }
          const key = item.Findex
          if (!map[key]) {
            map[key] = {
              title: key,
              items: []
            }
          }
          map[key].items.push(new Singer({
            name: item.Fsinger_name,
            id: item.Fsinger_mid
          }))
        })
        let ret = []
        let hot = []
        for (let key in map) {
          let val = map[key]
          if (val.title.match(/[a-zA-Z]/)) {
            ret.push(val)
          } else if (val.title === HOT_NAME) {
            hot.push(val)
          }
        }
        ret.sort((a, b) => {
          return a.title.charCodeAt(0) - b.title.charCodeAt(0)
        })
        return hot.concat(ret)
      },
      _calculateHeight(res) {
        if (res === 0) {
          this.domscroll._slide(false, 0, true, false)
          this.showTitle = 0
        } else {
          this.domscroll._slide(false, -this.domHeightArray[res - 1], true, false)
          this.showTitle = 1
        }
      },
      _updateSideBar(res) {
        // _lazyLoad()
        // console.log(res)
        if (res < 0 && this.lazyLoop !== 1) {
          this._lazyLoad()
        }
        this.showTitle = 0
        if (res >= 0) {
          this.scrollH = 0
          this.$refs.sList._countSite(0)
          this.$refs.sTitle._countSite(0)
          return
        }
        this.showTitle = 1
        for (let i = 0; i < this.domHeightArray.length; i++) {
          if (-res < this.domHeightArray[i]) {
            this.$refs.sList._countSite(i)
            this.$refs.sTitle._countSite(i)
            return
          }
        }
      },
      _touchStart(singer) {
        this.$router.push({
          path: `/singer/${singer.id}`
        })
        this.setSinger(singer)
      },
      ...mapMutations({
        setSinger: 'SET_SINGER'
      })
    },
    components: {
      VScroll,
      SList,
      STitle
    }
  }
</script>
<style scoped>
  .singerBody{
    position: fixed;
    top: 88px;
    bottom: 0;
    z-index: 1;
    width: 100%;
    min-width: 320px;
    /*padding-bottom: 60px;*/
    overflow: hidden;
    background:#222;
  }
  .singerList{
    width: 100%;
    padding-bottom: 20px;
  }
/*  .singerList:last-child{
    padding-bottom: 60px;
  }*/
  .singerList h3{
    width: 100%;
    height: 30px;
    line-height: 30px;
    background: #333;
    color: #999;
    font-size: 14px;
    text-indent: 20px;
  }
  .singerUl{
    width: 100%;
    overflow: hidden;
  }
  .singerUl li{
    width: 100%;
    height: 50px;
    padding: 20px 0 0 30px;
  }
  .singerUl li img{
    width: 50px;
    height: 50px;
    border-radius: 50%;
  }
  .img-warp{
    float: left;
    width: 50px;
    height: 50px;
    border-radius: 50%;
  }
  .singerUl li span{
    margin-left: 20px;
    font-size: 14px;
    color: #999;
  }
  .sideBar{
    position: fixed;
    z-index: 2;
    right: 0;
    top: 50%;
    transform: translate(0%, -50%);
    width: 20px;
    height: 410px;
    padding: 10px 0;
    border-radius: 10px;
    background: rgba(3,3,3,.2);
  }
  .titleBar{
    position: fixed;
    z-index: 2;
    top: 87px;
    left: 0;
    width: 100%;
    height: 30px;
    line-height: 30px;
    background: #333;
    color: #999;
    font-size: 14px;
    text-indent: 20px;
    opacity: 0;
  }
  .opacity{
    opacity: 1;
  }
</style>


